body {
 @include font-feature-settings("kern", "liga", "pnum");
 -webkit-font-smoothing: antialiased;
 color: $base-font-color;
 font-family: $base-font-family;
 font-size: $base-font-size;
 line-height: $base-line-height;
}

h1,
h2,
h3,
h4,
h5,
h6 {
 font-family: $heading-font-family;
 font-size: $base-font-size;
 line-height: $heading-line-height;
 margin: 0 0 $base-font-size;
 margin: 0 0 rem($base-font-size);
}
h1 {
	font-size: modular-scale(4);
 margin: 0;
}
h2 {
	font-size: modular-scale(3);
}
h3 {
	font-size: modular-scale(2);
}
h4 {
	font-size: modular-scale(1);
}
h5 {
	font-size: modular-scale(0);
}
h6 {
	font-size: modular-scale(-1);
}

p {
 margin: 0 0 $small-spacing;
}

a {
 color: $action-color;
 text-decoration: none;
 transition: color 0.4s ease;

 &:active,
 &:focus,
 &:hover {
 color: darken($action-color, 15%);
 }

 &:active,
 &:focus {
 outline: none;
 }
}

hr {
 border-bottom: $base-border;
 border-left: none;
 border-right: none;
 border-top: none;
 margin: $base-spacing 0;
}

img,
picture {
 margin: 0;
 max-width: 100%;
}

code, pre {
 font-family: SFMono-Regular,Consolas,Liberation Mono,Menlo,monospace,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol,Noto Color Emoji;
 -webkit-font-smoothing: auto;
 -moz-osx-font-smoothing: auto;
}
