table {
 @include font-feature-settings("kern", "liga", "tnum");
 border-collapse: collapse;
 margin: $small-spacing 0;
 table-layout: auto;
 width: 100%;
}

th {
 border-bottom: 1px solid darken($base-border-color, 15%);
 font-weight: 600;
 padding: $small-spacing 5px;
 text-align: left;
 white-space: nowrap;
}

td {
 border-bottom: $base-border;
 padding: $small-spacing 5px;
}

tr,
td,
th {
 vertical-align: middle;
 overflow-wrap: break-word;
}

// tablesorter specific css
.tablesorter {
 th:not(.sorter-false) {
 cursor: pointer;
 outline: none;
 position: relative;
		padding-left: 20px;
		padding-right: 20px;

 &.tablesorter-headerAsc {
 &:after {
 font-family: "Font Awesome 5 Free";
 content: "\f0de";
 position: absolute;
 right: 2%;
 top: 50%;
 margin-top: -7px;
 width: 14px;
 height: 14px;
 background-repeat: no-repeat;
 line-height: 14px;
 }
 }

 &.tablesorter-headerDesc {
 &:after {
 font-family: "Font Awesome 5 Free";
 content: "\f0dd";
 position: absolute;
 right: 2%;
 top: 50%;
 margin-top: -7px;
 width: 14px;
 height: 14px;
 background-repeat: no-repeat;
 line-height: 14px;
 }
 }

 &.tablesorter-headerUnSorted {
 &:after {
 font-family: "Font Awesome 5 Free";
 content: "\f0dc";
 position: absolute;
 right: 2%;
 top: 50%;
 margin-top: -7px;
 width: 14px;
 height: 14px;
 background-repeat: no-repeat;
 line-height: 14px;
 }
 }
 }
}
