ul li {
 &.nav-search-box {
 .aa-dropdown-menu {
 background-color: $base-background-color;
 box-shadow: 1px 1px 5px $medium-gray;
 min-width: 300px;
 margin-top: 10px;
 box-sizing: border-box;
 width: 460px;
 border-radius: 10px;
 }

 .aa-suggestion {
 cursor: pointer;

 p {
 margin: 0;
 }

 a {
 display: block;
 width: 100%;
 height: 100%;
 padding: 12px;

 .suggestion-no-reults {
 font-weight: bold;
 text-align: center;
 }

 .suggestion-content {
 color: $base-font-color;
 font-size: 13px;
 padding-top: 5px;

 &:active,
 &:focus,
 &:hover {
 color: darken($base-font-color, 15%);
 }
 }
 }

 .ais-Highlight {
 background-color: $blue-gray-100;
 font-weight: bold;
 font-style: normal;
 }
 }

 .aa-suggestion + .aa-suggestion {
 border-top: $base-border;
 }

 .aa-suggestion:hover, .aa-suggestion.aa-cursor {
 background-color: $base-background-color;
 }

 .aa-branding {
 text-align: right;

 img {
 max-height: 20px;
 padding-right: 8px;
 padding-bottom: 2px;
 }
 }
 }
}
